import { h, Component } from 'preact';
import { useEffect } from 'preact/hooks';
import util from 'preact-util';
import { observer } from 'mobx-preact';
import { Text, Localizer } from 'preact-i18n';
import { route } from 'preact-router';
import Markdown from 'preact-markdown';
import linkstate from 'linkstate';

import RaceTracking from '../../components/public/raceTracking.js';

import mu from '../../lib/musher-util';
import style from './style.css';

const MARGIN_TOP = mu.marginTop(false);
const MARGIN_TOP_BACK = mu.marginTopBack(false);
const MARGIN_BOTTOM = mu.marginBottom();
const SUB_MENU_MARGIN_TOP = mu.subMenuMarginTop();

const initialState = {};

@observer
class TvTracking extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...initialState,
        };
        this.updateTimer = {};
    }

    toggleFocusOnMusher = (e) => {
		const musherId = e.target.parentElement.dataset.musherid || e.target.dataset.musherid;
		const { appState, raceStore } = this.props.stores;
		appState.toggleFocusOnMusher(musherId);

		const { focusOnMushers } = appState;

        const { race } = raceStore;
        if (race.liveRace > 0) {
            raceStore.getRaceTrackerActiveTeams(null, focusOnMushers);
        } else {
            raceStore.getWorkoutSummaryActiveTeams(null, focusOnMushers);
        }
	}

    toggleBib = () => {
        const { bib } = this.state;
        const { appState, raceStore } = this.props.stores;
        const musher = raceStore.getMusherByBibId(bib);
		appState.toggleFocusOnMusher(musher.id);

        const { focusOnMushers } = appState;
        const { race } = raceStore;
        if (race.liveRace > 0) {
            raceStore.getRaceTrackerActiveTeams(null, focusOnMushers);
        } else {
            raceStore.getWorkoutSummaryActiveTeams(null, focusOnMushers);
        }
    }

    checkForEnter = (e) => {
        if (e.key === 'Enter') {
            this.toggleBib();
        }
    }

    toggleShowDrawerReplay = () => {
        const { showDrawerReplay } = this.state;
        this.setState({
            showDrawerReplay: !showDrawerReplay,
        });
    }

    back = () => {
        const { history } = this.props.stores;
        history.goBack();
    }


    render() {
        const { showDrawerReplay, historySteps = 40 } = this.state;
        const { userStore, raceStore, appState } = this.props.stores;
        const { user = {}, teams = [], isAdmin } = userStore;

        const {
            raceid,
            classid,
            path,
            position = 'right',
            bibs = '',
            showHeader = false,
            showFooter = false,
        } = this.props;
        const { focusOnMushers = [] } = appState;

        const { race } = raceStore;
        const selectedClass = raceStore.getClassById(classid);

        return (<>
            <style dangerouslySetInnerHTML={{__html: `
                body, .bootstrap, .bootstrap-dark {
                    background-color: transparent !important;
                }
            `}} />
            <div class={`fixed-top ${style.hoverElement}`} style='height: 100px;'>
                <div class={`${style.hoverelementonly}`}>
                    <div class={`row`}>
                        {race.classes && race.classes.sort((a, b) => a.distance - b.distance).map((currentClass, idx) => <>
                            <div class='col text-center'>
                                <a
                                    class={`btn btn-block ${currentClass.id === classid ? 'btn-success' : 'btn-outline-success'} text-nowrap`}
                                    style='text-overflow: ellipsis; overflow: hidden;'
                                    data-id={currentClass.id}
                                    href={`/tv/tracking/${race.id}/${currentClass.id}/${position}`}
                                >
                                    {currentClass.title}
                                </a>

                                <a
                                    class={`btn btn-link text-nowrap text-center`}
                                    style='text-overflow: ellipsis; overflow: hidden;'
                                    data-id={currentClass.id}
                                    href={`/tv/tracking/${race.id}/${currentClass.id}/center`}
                                >
                                    center
                                </a>
                                <a
                                    class={`btn btn-link text-nowrap ${position === 'left' ? 'float-right' : 'float-left'}`}
                                    style='text-overflow: ellipsis; overflow: hidden;'
                                    data-id={currentClass.id}
                                    href={`/tv/tracking/${race.id}/${currentClass.id}/${position === 'left' ? 'right' : 'left'}`}
                                >
                                    {position === 'left' ? <>
                                        right <i class='fas fa-angle-right' />
                                    </> : <>
                                        <i class='fas fa-angle-left' /> left
                                    </>}
                                </a>
                            </div>
                        </>)}
                    </div>
                    <div class={`row`}>
                        {/* <div class='col-4 form-inline align-top d-flex align-items-start'>
                            <div class='form-group'>
                                <label class='text-success mr-2'>Enter Bib</label>
                                <input
                                    class='form-control form-control-lg text-success border-success'
                                    style='background-color: inherit;'
                                    type='text'
                                    onInput={linkstate(this, 'bib')}
                                    onKeyDown={this.checkForEnter}
                                />
                                <button
                                    type='button'
                                    class='btn btn-lg btn-outline-success'
                                    onClick={this.toggleBib}
                                >Show Bib</button>
                            </div>
                        </div> */}
                        <div class='col-2 text-success' style='font-size: 1.3em;'>
                            <button
                                type='button'
                                class='btn btn-lg btn-outline-success'
                                onClick={this.toggleShowDrawerReplay}
                            >Toggle replay</button>
                        </div>
                        <div class='col-2 text-success' style='font-size: 1.3em;'>
                            <label class='text-success mr-2'>Sec between steps</label>
                            <input
                                class='form-control form-control-lg text-success border-success'
                                style='background-color: inherit;'
                                type='text'
                                onInput={linkstate(this, 'historySteps')}
                                value={historySteps}
                            />
                        </div>

                        {focusOnMushers && <div class='col-4 text-success' style='font-size: 1.3em;'>
                            {focusOnMushers && focusOnMushers.map(musherId => {
                                const musher = raceStore.getMusherById(musherId);
                                return <>                                    
                                    <div>
                                        <span class='float-left mr-2' onClick={this.toggleFocusOnMusher} data-musherid={musher.id}>{focusOnMushers.indexOf(musher.id) > -1 ? <i class='fas fa-star text-warning' /> : <i class='far fa-star' />}</span>
                                        <nobr>
                                            <span class='text-right' style='width: 50px; display: inline-block;'>{musher.bib}</span> {musher.flag} {musher.firstname} {musher.lastname}<br />
                                            {/* <small class='text-muted'>{musher.sportsClub}</small><br /> */}
                                        </nobr>
                                    </div>
                                </>
                            })}
                        </div>}
                    </div>
                </div>
            </div>

			<div class='container-fluid' style={`margin-bottom: ${MARGIN_BOTTOM}; margin-top: ${MARGIN_TOP}; height: calc(100% - ${MARGIN_BOTTOM} - ${MARGIN_TOP});`}>
                <div class='row'>
                    <div class={position === 'center' ? 'offset-1 col-10' : (position === 'left' ? 'col-4 ml-5' : 'col-4 offset-8 mr-5')}>
                        {showHeader && <div
                            class='px-4 py-2 overflow-hidden'
                            style='
                                background-color: #FB4114; 
                                color: #ffffff; 
                                border-radius: 10px 10px 0 0; 
                                font-size: 1.8em;
                            '
                        >
                            <span class='float-right font-weight-light'>{selectedClass.title}</span>
                            GPS Tracking 
                        </div>}
                        <div
                            class='mr-5 p-0 position-relative w-100'
                            style='
                                height: 80vh;
                                background: rgba(0, 0, 0, 0.3);
                                background-color: #1C1F4C;
                                background-image: url("https://themusher.app/assets/tv-single-background.jpg");
                                background-size: cover !important;
                                background-position: 0 0;    
                            '
                        >
                            <RaceTracking
                                stores={this.props.stores}
                                raceid={raceid}
                                showHeader={false}
                                classid={selectedClass.id}
                                drawerClassNames='position-absolute'
                                drawerWidth='100%'
                                showDrawerReplay={showDrawerReplay}
                                largeMarkers={true}
                                filterByBibs={bibs ? bibs.split(',').map(e => parseInt(e, 10)) : ''}
                                historySteps={parseInt(historySteps, 10)}
                                isAnimatedMarkers={true}
                            />
                        </div>
                        {showFooter && <div
                            class='px-4 py-2 overflow-hidden text-center'
                            style='
                                background-color: #FB4114; 
                                color: #ffffff; 
                                border-radius: 0 0 10px 10px;
                                font-size: 1.4em;
                            '
                        >
                            <nobr class='mr-4'>QPaws app</nobr>
                                            
                            <img src='https://themusher.app/assets/appstore-download.png' height='40' class='ml-5 mr-1' />
                            <img src='https://themusher.app/assets/playstore-download.png' height='40' class='mx-1' />
                        </div>}
                    </div>
                </div>
            </div>
        </>);
    }
}

export default TvTracking;
